.app {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: #e2e2e2;
  color: #313335;

  .appContainer {
    width: 1200px;
    height: 710px;
    border-radius: 10px;
    background-color: #18191d;

    .headerContainer {
      height: 10%;
      position: relative;
    }
    .bodyContainer {
      display: flex;
      flex-direction: row;
      height: 90%;
      width: 100%;
    }
  }
}